.pricing{
	float: left;
	display: block;
	width: 100%;
	background-color: #F1F1F2;
	height: 800px;
	position: relative;
	text-align: center;
	.mount{
    	bottom: 0;
		position: absolute;

		background-position: 50% 100%;
    }
}
.tabs{
	display: inline-block;
	width: 75%;
	float: none;
	margin:50px auto;
	position: relative;
	z-index: 10;
}
.tabs_items{
	text-align: center;
	background-color: #FFF;
	width: 30.3%;
	float: left;
	position: relative;
	display: block;
	height: 440px;
	margin-right: 39px;
	&:last-of-type{
		margin-right: 0;
	}
	&:hover{
		.trial{
			background-color: #4b5158;
			color: #FFF;
		}
		.purchase{
			background-color: #fdc12e;
		}
		.rectangle{
			background-color: #fdc12e;
			color: #4b5158;
		}
	}
}
.trial{
	font-size: 30px;
	font-family: $rl;
	color: #4b5158;
	text-transform: uppercase;
	float: left;
	display: inline-block;
	width: 100%;
	margin: 0;
	height: 100px;
	padding: 30px 0;
    box-sizing: border-box;

}
.rectangle{
	height: 43px;
	background-color: #4b5158;
	float: left;
	display: block;
	width: 100%;
	line-height: 43px;
	color: #fdc12e;
	font-size: 18px;
	font-family: $rl;
	b{
		font-family: $rb;
	}

}
.tabs-text{
	font-family: $rr;
	font-size: 18px;
	color: #a6a6a6;
	float: none;
	display: inline-block;
	width: 100%;
	position: relative;
	margin: auto 0;
	line-height: 25px;
	padding: 30px 35px;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;

}
.button-purchase{
	width: 100%;
	float: left;
	height: 70px;
	position: absolute;
	bottom: 0;
	display: block;
	padding: 20px 0;
}
.purchase{
	text-transform: uppercase;
	width: 75%;
	font-size: 14px;
   	height: 25px;
   	line-height: 25px;
    color: #4b5158;
    border: 4px solid #fdc12e;
    padding: 10px;
    display: inline-block;

    float: none;
    margin: 0 auto;
}
.pricing__cont{
	width: 100%;
	float: left;
	display: block;
	background-color: #FFC203;
	position: relative;
	text-align: center;
	height: auto;
	padding: 20px 0 50px;
	a{
		font-size: 36px;
		color: #4b5158;
		font-family: $rb;
		&:hover{
			color: #5c1414;
		}
	}


}
